"use client"

import { useState, useEffect } from "react"

export interface MCPServer {
  id: string
  name: string
  url: string
  apiKey: string
  isActive: boolean
}

export function useMCPConfig() {
  const [servers, setServers] = useState<MCPServer[]>(() => {
    if (typeof window !== "undefined") {
      const saved = localStorage.getItem("mcp-servers")
      if (saved) {
        return JSON.parse(saved)
      }
    }
    return [{
      id: "default",
      name: "Default MCP Server",
      url: "https://api.mcp.example.com",
      apiKey: "",
      isActive: true
    }]
  })

  useEffect(() => {
    if (typeof window !== "undefined") {
      localStorage.setItem("mcp-servers", JSON.stringify(servers))
    }
  }, [servers])

  const addServer = () => {
    const newServer: MCPServer = {
      id: `server-${Date.now()}`,
      name: "New MCP Server",
      url: "",
      apiKey: "",
      isActive: false
    }
    setServers([...servers, newServer])
  }

  const removeServer = (id: string) => {
    setServers(servers.filter(server => server.id !== id))
  }

  const updateServer = (id: string, updates: Partial<MCPServer>) => {
    setServers(servers.map(server => 
      server.id === id ? { ...server, ...updates } : server
    ))
  }

  const setActiveServer = (id: string) => {
    setServers(servers.map(server => ({
      ...server,
      isActive: server.id === id
    })))
  }

  const getActiveServer = () => {
    return servers.find(server => server.isActive)
  }

  return {
    servers,
    addServer,
    removeServer,
    updateServer,
    setActiveServer,
    getActiveServer
  }
}
