"use client"

import * as React from "react"
import { motion } from "framer-motion"
import { Button } from "@/components/ui/button"
import { cn } from "@/lib/utils"

interface AnimatedButtonProps extends React.ComponentPropsWithoutRef<typeof Button> {
  scaleOnHover?: boolean
  glowOnHover?: boolean
}

const AnimatedButton = React.forwardRef<
  HTMLButtonElement,
  AnimatedButtonProps
>(({ className, scaleOnHover = true, glowOnHover = false, ...props }, ref) => {
  return (
    <motion.div
      whileHover={scaleOnHover ? { scale: 1.02 } : undefined}
      whileTap={{ scale: 0.98 }}
    >
      <Button
        ref={ref}
        className={cn(
          className,
          glowOnHover && "transition-shadow hover:shadow-[0_0_15px_rgba(59,130,246,0.5)]"
        )}
        {...props}
      />
    </motion.div>
  )
})
AnimatedButton.displayName = "AnimatedButton"

export { AnimatedButton }
