"use client"

import { useState, useCallback } from "react"
import { Button } from "@/components/ui/button"
import { Separator } from "@/components/ui/separator"
import { ScrollArea } from "@/components/ui/scroll-area"
import Editor from "@monaco-editor/react"
import { FileIcon, GearIcon, GlobeIcon, LayoutIcon } from "@radix-ui/react-icons"
import { useAIAutocomplete } from "@/hooks/use-ai-autocomplete"
import { AISuggestions } from "@/components/ai-suggestions"
import { Header } from "@/components/header"
import { StatusBar } from "@/components/status-bar"
import { MCPSettings } from "@/components/mcp-settings"
import { motion, AnimatePresence } from "framer-motion"
import { fluentFade, fluentSlide } from "@/lib/animations"

export default function Home() {
  const [activeTab, setActiveTab] = useState("files")
  const [showMCPSettings, setShowMCPSettings] = useState(false)
  const [editorContent, setEditorContent] = useState("// Start coding here...")
  const { suggestions, isLoading, getSuggestions } = useAIAutocomplete()

  const handleEditorChange = useCallback((value: string | undefined) => {
    if (value) {
      setEditorContent(value)
      getSuggestions(value)
    }
  }, [getSuggestions])

  const handleSuggestionSelect = useCallback((suggestion: string) => {
    setEditorContent(prev => prev + "\n" + suggestion)
  }, [])

  return (
    <div className="flex flex-col h-screen">
      <Header />
      <div className="flex flex-1">
        {/* Activity Bar */}
        <motion.div 
          className="w-14 bg-zinc-900 flex flex-col items-center py-4 border-r border-zinc-800"
          variants={fluentFade}
          initial="initial"
          animate="animate"
        >
          <Button
            variant="ghost"
            size="icon"
            className={`mb-2 ${activeTab === "files" ? "bg-zinc-800" : ""}`}
            onClick={() => setActiveTab("files")}
          >
            <FileIcon className="h-5 w-5" />
          </Button>
          <Button
            variant="ghost"
            size="icon"
            className={`mb-2 ${activeTab === "globe" ? "bg-zinc-800" : ""}`}
            onClick={() => setActiveTab("globe")}
          >
            <GlobeIcon className="h-5 w-5" />
          </Button>
          <Button
            variant="ghost"
            size="icon"
            className={`mb-2 ${activeTab === "window" ? "bg-zinc-800" : ""}`}
            onClick={() => setActiveTab("window")}
          >
            <LayoutIcon className="h-5 w-5" />
          </Button>
          <div className="flex-1" />
          <Button
            variant="ghost"
            size="icon"
            className={`${activeTab === "settings" ? "bg-zinc-800" : ""}`}
            onClick={() => setActiveTab("settings")}
          >
            <GearIcon className="h-5 w-5" />
          </Button>
        </motion.div>

        {/* Sidebar */}
        <motion.div 
          className="w-64 bg-zinc-900 border-r border-zinc-800"
          variants={fluentFade}
          initial="initial"
          animate="animate"
        >
          <div className="p-4">
            <h2 className="text-sm font-semibold mb-4">
              {activeTab === "files" && "Explorer"}
              {activeTab === "globe" && "AI Assistant"}
              {activeTab === "window" && "Extra Tools"}
              {activeTab === "settings" && "Settings"}
            </h2>
            <ScrollArea className="h-[calc(100vh-8rem)]">
              <AnimatePresence mode="wait">
                {activeTab === "files" && (
                  <motion.div
                    className="space-y-2"
                    variants={fluentSlide}
                    initial="initial"
                    animate="animate"
                    exit="exit"
                  >
                    <div className="flex items-center space-x-2 text-sm">
                      <FileIcon className="h-4 w-4" />
                      <span>index.tsx</span>
                    </div>
                  </motion.div>
                )}
                {activeTab === "globe" && (
                  <motion.div
                    className="space-y-4"
                    variants={fluentSlide}
                    initial="initial"
                    animate="animate"
                    exit="exit"
                  >
                    <div className="text-sm font-medium">AI Assistant</div>
                    <AISuggestions
                      suggestions={suggestions}
                      isLoading={isLoading}
                      onSelect={handleSuggestionSelect}
                    />
                  </motion.div>
                )}
                {activeTab === "window" && (
                  <motion.div
                    className="space-y-2"
                    variants={fluentSlide}
                    initial="initial"
                    animate="animate"
                    exit="exit"
                  >
                    <Button variant="ghost" className="w-full justify-start text-sm">
                      Terminal
                    </Button>
                    <Button variant="ghost" className="w-full justify-start text-sm">
                      Debug Console
                    </Button>
                    <Button variant="ghost" className="w-full justify-start text-sm">
                      Problems
                    </Button>
                  </motion.div>
                )}
                {activeTab === "settings" && (
                  <motion.div
                    className="space-y-4"
                    variants={fluentSlide}
                    initial="initial"
                    animate="animate"
                    exit="exit"
                  >
                    <div className="space-y-2">
                      <Button variant="ghost" className="w-full justify-start text-sm">
                        Color Theme
                      </Button>
                      <Button variant="ghost" className="w-full justify-start text-sm">
                        File Icon Theme
                      </Button>
                      <Button variant="ghost" className="w-full justify-start text-sm">
                        Keyboard Shortcuts
                      </Button>
                      <Button 
                        variant="ghost" 
                        className="w-full justify-start text-sm text-blue-400 hover:text-blue-300"
                        onClick={() => setShowMCPSettings(true)}
                      >
                        MCP Settings
                      </Button>
                    </div>
                    <AnimatePresence>
                      {showMCPSettings && (
                        <motion.div
                          className="mt-4"
                          variants={fluentFade}
                          initial="initial"
                          animate="animate"
                          exit="exit"
                        >
                          <MCPSettings />
                        </motion.div>
                      )}
                    </AnimatePresence>
                  </motion.div>
                )}
              </AnimatePresence>
            </ScrollArea>
          </div>
        </motion.div>

        {/* Editor */}
        <motion.div 
          className="flex-1 bg-zinc-950"
          variants={fluentFade}
          initial="initial"
          animate="animate"
        >
          <div className="h-full">
            <Editor
              height="100%"
              defaultLanguage="javascript"
              value={editorContent}
              onChange={handleEditorChange}
              theme="vs-dark"
              options={{
                fontSize: 14,
                minimap: { enabled: true },
                scrollBeyondLastLine: false,
                automaticLayout: true,
                suggestOnTriggerCharacters: true,
                quickSuggestions: true,
                wordBasedSuggestions: true,
                lineNumbers: "on",
                roundedSelection: false,
                selectOnLineNumbers: true,
                cursorStyle: "line",
                cursorWidth: 1,
                cursorBlinking: "blink",
                renderWhitespace: "none",
                contextmenu: true,
                mouseWheelZoom: true,
                folding: true,
                showFoldingControls: "always",
                renderControlCharacters: true,
                renderIndentGuides: true,
                overviewRulerBorder: false,
                scrollBeyondLastColumn: 5,
              }}
            />
          </div>
        </motion.div>
      </div>
      <StatusBar />
    </div>
  )
}
