"use client"

import { AnimatedButton } from "@/components/ui/animated-button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { ScrollArea } from "@/components/ui/scroll-area"
import { GearIcon, PlusIcon, TrashIcon, RocketIcon } from "@radix-ui/react-icons"
import { useMCPConfig } from "@/hooks/use-mcp-config"
import { Switch } from "@/components/ui/switch"
import { motion, AnimatePresence } from "framer-motion"
import { fluentScale, fluentSlide, fluentFade } from "@/lib/animations"

export function MCPSettings() {
  const { 
    servers, 
    addServer, 
    removeServer, 
    updateServer, 
    setActiveServer 
  } = useMCPConfig()

  return (
    <div className="p-4 space-y-4">
      <div className="flex items-center justify-between mb-4">
        <h2 className="text-sm font-semibold flex items-center">
          <RocketIcon className="mr-2 h-4 w-4" />
          MCP Settings
        </h2>
        <motion.div
          whileHover={{ scale: 1.02 }}
          whileTap={{ scale: 0.98 }}
        >
          <AnimatedButton
            variant="outline"
            size="sm"
            onClick={addServer}
            className="h-8 px-2 bg-blue-500/10 hover:bg-blue-500/20 text-blue-400"
            glowOnHover
          >
            <PlusIcon className="h-4 w-4 mr-1" />
            Add Server
          </AnimatedButton>
        </motion.div>
      </div>
      <ScrollArea className="h-[calc(100vh-12rem)]">
        <AnimatePresence mode="popLayout">
          {servers.map((server) => (
            <motion.div
              key={server.id}
              className="space-y-3 p-3 rounded-md bg-zinc-900"
              variants={fluentScale}
              initial="initial"
              animate="animate"
              exit="exit"
              layout
            >
              <div className="flex items-center justify-between">
                <div className="flex items-center space-x-2">
                  <Switch
                    checked={server.isActive}
                    onCheckedChange={() => setActiveServer(server.id)}
                  />
                  <Label className="text-xs font-medium">{server.name}</Label>
                </div>
                <motion.div
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                >
                  <AnimatedButton
                    variant="ghost"
                    size="sm"
                    onClick={() => removeServer(server.id)}
                    className="h-6 px-2 text-red-500 hover:bg-red-500/10 hover:text-red-400"
                  >
                    <TrashIcon className="h-4 w-4" />
                  </AnimatedButton>
                </motion.div>
              </div>
              <motion.div 
                className="space-y-2"
                variants={fluentSlide}
                initial="initial"
                animate="animate"
                exit="exit"
              >
                <div className="space-y-1">
                  <Label className="text-xs">Name</Label>
                  <Input
                    value={server.name}
                    onChange={(e) => updateServer(server.id, { name: e.target.value })}
                    className="h-8 bg-zinc-950"
                    placeholder="Server name"
                  />
                </div>
                <div className="space-y-1">
                  <Label className="text-xs">URL</Label>
                  <Input
                    value={server.url}
                    onChange={(e) => updateServer(server.id, { url: e.target.value })}
                    className="h-8 bg-zinc-950"
                    placeholder="https://..."
                  />
                </div>
                <div className="space-y-1">
                  <Label className="text-xs">API Key</Label>
                  <Input
                    type="password"
                    value={server.apiKey}
                    onChange={(e) => updateServer(server.id, { apiKey: e.target.value })}
                    className="h-8 bg-zinc-950"
                    placeholder="Enter API key"
                  />
                </div>
              </motion.div>
            </motion.div>
          ))}
        </AnimatePresence>
      </ScrollArea>
    </div>
  )
}
