"use client"

import { Button } from "@/components/ui/button"
import { ScrollArea } from "@/components/ui/scroll-area"
import { Skeleton } from "@/components/ui/skeleton"

interface AISuggestionsProps {
  suggestions: string[]
  isLoading: boolean
  onSelect: (suggestion: string) => void
}

export function AISuggestions({ suggestions, isLoading, onSelect }: AISuggestionsProps) {
  if (isLoading) {
    return (
      <div className="space-y-2">
        <Skeleton className="h-8 w-full bg-zinc-800" />
        <Skeleton className="h-8 w-full bg-zinc-800" />
        <Skeleton className="h-8 w-full bg-zinc-800" />
      </div>
    )
  }

  if (suggestions.length === 0) {
    return (
      <div className="text-sm text-zinc-400 p-4">
        No suggestions available. Keep typing to get AI assistance...
      </div>
    )
  }

  return (
    <ScrollArea className="h-[200px]">
      <div className="space-y-2 p-2">
        {suggestions.map((suggestion, index) => (
          <Button
            key={index}
            variant="ghost"
            className="w-full justify-start text-sm font-mono hover:bg-zinc-800"
            onClick={() => onSelect(suggestion)}
          >
            {suggestion}
          </Button>
        ))}
      </div>
    </ScrollArea>
  )
}
